html, body {
    padding: 0;
    margin: 0;
    overflow: hidden;
    background-color: grey;
    display: flex;
    height: 100%;
    width: 100%;
    align-items: center;
    justify-items: center;
}
canvas {
    display: block;
    margin: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
